<template>
 <div>
      <el-dialog
            title="提报服务"
            width="700px"
            top="30px"
            :visible.sync="visible"
            :append-to-body="true"
            :close-on-click-modal="false"
            @close="$emit('update:show', false)"
            :show="show">
            <div class="dialog-tbfw">
               <form action="">
                   <div>
                       <h4>提报说明:</h4>
                       <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="textarea"></el-input>
                   </div>
                   <div>
                       <el-upload
                            class="upload-demo"
                            ref="uploadfile"
                            :action="upload_url"
                            :auto-upload="false"
                            :before-upload="newFiles"
                            multiple>
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">实验信息附件上传，只能传(.file)文件</div>
                        </el-upload>
                   </div>
                   <div>
                       <el-button size="mini" type="primary" @click="handleCommit">提交</el-button>&#x3000;
                       <el-button size="mini" type="primary" @click="handleClose">取消</el-button>
                   </div>
               </form>
            </div>
        </el-dialog>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         visible:this.show,
         upload_url:'123',
         uploadForm: new FormData(),
         textarea:''
     }
   },
   methods:{
       handleClose(){  
           this.visible = false
       },
       handleCommit(){
           this.$refs.uploadfile.submit();
           console.log( this.uploadForm);
       },
       newFiles(file){
          this.uploadForm.append('file[]', file);
          return false
       }
   },
   props:{
        show: {
            type: Boolean,
            default:false
        }
   },
   watch: {
        show (n,o) {
            this.visible = this.show;
            if(!n){
              
            }else{

            }
        }
    },
   components: {}
 }
</script>

<style scoped>
.dialog-tbfw{
    padding:15px 30px;
}
 
</style>



